import objectPath from "object-path"

declare const Application: ISparseWx

// @ts-ignore
import dset from "dset"

export function push(referencePropComplex: string[], value: any) {
  const reference = referencePropComplex[0]
  const property = referencePropComplex[1]

  // @ts-ignore
  let obj = JSON.parse(globalThis.userProperties.getProperty(reference))
  if (obj == null) {
    obj = {}
  }
  if (!!property) {
    dset(obj, property, value)
  } else {
    obj = value
  }
  globalThis.userProperties.setProperty(reference, JSON.stringify(obj))
}

export function fetch(reference: string, property?: any) {
  // @ts-ignore
  let obj = JSON.parse(globalThis.userProperties.getProperty(reference))
  if (obj == null) {
    obj = {}
    globalThis.userProperties.setProperty(reference, "{}")
    return null
  }
  if (property == null) {
    return obj
  }
  return objectPath.withInheritedProps.get(obj, property)
}
